@use 'abstracts/mixins' as *;

body {
  background: var(--white);
}

.jumbotron {
  padding-top: 134px;

  @include md {
    padding-top: 76px;
  }

  .section-title {
    color: var(--mid-green);
    font-size: 20px;
    margin-bottom: 24px;
    padding-left: 0;
    margin-left: -1px;
    letter-spacing: 2px;

    &::before {
      font-size: 20px;
      margin-right: 4px;

      @include md {
        font-size: 16px;
      }
    }

    @include md {
      font-size: 16px;
      margin-bottom: 22px;
      letter-spacing: 1.6px;
    }
  }

  &__subtitle {
    text-transform: uppercase;
    font-size: 30px;
    letter-spacing: 1.5px;
    line-height: 46px;
    color: var(--mid-green);
    font-weight: 300;
    margin-bottom: 113px;
    max-width: 940px;
    margin-left: -2px;

    @include md {
      font-size: 20px;
      letter-spacing: 1px;
      line-height: 28px;
      padding-left: 2px;
      margin-bottom: 83px;
    }

    span {
      color: var(--gold);
    }
  }
}

.form {
  background: url('./../assets/contact-bg.webp') no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 100px;
  padding-bottom: 92px;
  min-height: 734px;
  text-align: center;

  @include md {
    padding-top: 69px;
    min-height: 734px;
    background-attachment: scroll !important;
    background-position: center !important;
  }

  &__logo {
    margin-bottom: 50px;

    @include md {
      margin-bottom: 42px;
    }
  }

  &__top {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 55px;
  }

  &__mandatory {
    font: normal normal normal 16px/24px Brandon Grotesque;
    letter-spacing: 1.6px;
    color: #ffffff;
    text-transform: uppercase;
  }

  &__description {
    text-align: center;
    font-family: var(--unnamed-font-family-garamond-premier-pro);
    font-weight: 300;
    font-size: 24px;
    line-height: 30px;
    letter-spacing: 1.2px;
    margin-bottom: 70px;
    color: var(--white);

    @include md {
      font-size: 16px;
      margin-bottom: 40px;
      line-height: 22px;
      letter-spacing: 0.8px;
    }
  }

  &__title {
    text-transform: uppercase;
    font-size: 28px;
    letter-spacing: 2.8px;
    color: var(--light-grey);
    margin-bottom: 46px;

    @include md {
      font-size: 24px;
      letter-spacing: 2.4px;
      margin-bottom: 23px;
      line-height: 26px;
    }
  }

  &__two-columns {
    display: flex;
    gap: 20px;

    @include md {
      gap: 0;
      flex-wrap: wrap;
    }
  }

  form {
    text-align: left;
  }

  &__input {
    border: 0;
    border-bottom: 1px solid var(--white);
    padding: 14px 0;
    font-size: 16px;
    letter-spacing: 1.6px;
    color: var(--white);
    width: 100%;
    background: transparent;
    text-transform: uppercase;
    outline: none;
    margin-bottom: 54px;

    &::placeholder {
      color: var(--white);
    }

    @include md {
      margin-bottom: 34px;
    }
  }

  &__select {
    width: 100%;
    border: 1px solid var(--white);
    background: transparent;
    font-size: 16px;
    letter-spacing: 1.6px;
    padding: 8px 20px;
    color: var(--white);
    margin-bottom: 62px;
    text-transform: uppercase;
    appearance: none;
    outline: none;

    @include md {
      margin-bottom: 34px;
    }

    &-container {
      position: relative;
      width: 70%;
      max-width: 690px;

      @include md {
        width: 100%;
      }

      &::after {
        content: url('./../assets/select-arrow.svg');
        position: absolute;
        top: 10px;
        right: 10px;
      }
    }
  }
  &__button-container {
    max-width: 340px;
    margin-top: 14px;

    @include md {
      max-width: 251px;
    }

    .button-outlined {
      @include md {
        padding-left: 11px;
        font-size: 10px;
        letter-spacing: 1px;
        height: 39px;
      }
    }
  }
}

.dropdown {
  position: relative;
  @include md {
    margin-bottom: 30px;
  }

  &__button {
    position: relative;
    display: flex;
    align-items: center;
    font-size: 16px;
    letter-spacing: 1.6px;
    color: #ffffff;
    text-transform: uppercase;
    width: 100%;
    background: transparent;
    border: 1px solid #ffffff;
    height: 36px;
    padding-left: 20px;
    cursor: pointer;
    text-align: left;

    @include md {
      height: 48px;
    }

    &_active {
      background: #162a26;
      &::after {
        transform: translateY(-50%) rotate(-180deg);
      }
    }
  }
  &__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    position: absolute;
    left: 0;
    top: 36px;
    @include md {
      top: 48px;
    }

    border: 1px solid #fff;
    background: #162a26;
    overflow: hidden;
    width: 100%;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    transition: 200ms ease;
    font-size: 16px;
    letter-spacing: 1.6px;
    color: #ffffff;
    text-transform: uppercase;

    &_visible {
      opacity: 1;
      visibility: visible;
    }

    &-item {
      margin: 0;
      padding: 0;
      padding: 15px;
      cursor: pointer;
      font-size: 15px;
      line-height: 18px;
      color: #fff;
      transition: 200ms ease;
      &_active {
        color: var(--gold);
      }
      &:hover {
        color: var(--gold);
      }
    }
  }
  &__input {
    &_hidden {
      display: none;
    }
  }
}

.form-success-message {
  display: none;
  text-align: left;
  font: normal normal normal 16px/24px Brandon Grotesque;
  letter-spacing: 1.6px;
  color: #ffffff;
  padding-top: 60px;
}

iframe {
  width: 100%;
  height: 1325px;
  margin-bottom: -5px;
}
