body {
  background: var(--white);
}

.jumbotron {
  padding-top: 134px;
}

@media (width <= 992px) {
  .jumbotron {
    padding-top: 76px;
  }
}

.jumbotron .section-title {
  color: var(--mid-green);
  letter-spacing: 2px;
  margin-bottom: 24px;
  margin-left: -1px;
  padding-left: 0;
  font-size: 20px;
}

.jumbotron .section-title:before {
  margin-right: 4px;
  font-size: 20px;
}

@media (width <= 992px) {
  .jumbotron .section-title:before {
    font-size: 16px;
  }

  .jumbotron .section-title {
    letter-spacing: 1.6px;
    margin-bottom: 22px;
    font-size: 16px;
  }
}

.jumbotron__subtitle {
  text-transform: uppercase;
  letter-spacing: 1.5px;
  color: var(--mid-green);
  max-width: 940px;
  margin-bottom: 113px;
  margin-left: -2px;
  font-size: 30px;
  font-weight: 300;
  line-height: 46px;
}

@media (width <= 992px) {
  .jumbotron__subtitle {
    letter-spacing: 1px;
    margin-bottom: 83px;
    padding-left: 2px;
    font-size: 20px;
    line-height: 28px;
  }
}

.jumbotron__subtitle span {
  color: var(--gold);
}

.form {
  text-align: center;
  background: url("contact-bg.8f72e459.webp") center / cover no-repeat fixed;
  min-height: 734px;
  padding-top: 100px;
  padding-bottom: 92px;
}

@media (width <= 992px) {
  .form {
    min-height: 734px;
    padding-top: 69px;
    background-position: center !important;
    background-attachment: scroll !important;
  }
}

.form__logo {
  margin-bottom: 50px;
}

@media (width <= 992px) {
  .form__logo {
    margin-bottom: 42px;
  }
}

.form__top {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 55px;
  display: flex;
}

.form__mandatory {
  letter-spacing: 1.6px;
  color: #fff;
  text-transform: uppercase;
  font: 16px / 24px Brandon Grotesque;
}

.form__description {
  text-align: center;
  font-family: var(--unnamed-font-family-garamond-premier-pro);
  letter-spacing: 1.2px;
  color: var(--white);
  margin-bottom: 70px;
  font-size: 24px;
  font-weight: 300;
  line-height: 30px;
}

@media (width <= 992px) {
  .form__description {
    letter-spacing: .8px;
    margin-bottom: 40px;
    font-size: 16px;
    line-height: 22px;
  }
}

.form__title {
  text-transform: uppercase;
  letter-spacing: 2.8px;
  color: var(--light-grey);
  margin-bottom: 46px;
  font-size: 28px;
}

@media (width <= 992px) {
  .form__title {
    letter-spacing: 2.4px;
    margin-bottom: 23px;
    font-size: 24px;
    line-height: 26px;
  }
}

.form__two-columns {
  gap: 20px;
  display: flex;
}

@media (width <= 992px) {
  .form__two-columns {
    flex-wrap: wrap;
    gap: 0;
  }
}

.form form {
  text-align: left;
}

.form__input {
  border: 0;
  border-bottom: 1px solid var(--white);
  letter-spacing: 1.6px;
  color: var(--white);
  text-transform: uppercase;
  background: none;
  outline: none;
  width: 100%;
  margin-bottom: 54px;
  padding: 14px 0;
  font-size: 16px;
}

.form__input::placeholder {
  color: var(--white);
}

@media (width <= 992px) {
  .form__input {
    margin-bottom: 34px;
  }
}

.form__select {
  border: 1px solid var(--white);
  letter-spacing: 1.6px;
  color: var(--white);
  text-transform: uppercase;
  appearance: none;
  background: none;
  outline: none;
  width: 100%;
  margin-bottom: 62px;
  padding: 8px 20px;
  font-size: 16px;
}

@media (width <= 992px) {
  .form__select {
    margin-bottom: 34px;
  }
}

.form__select-container {
  width: 70%;
  max-width: 690px;
  position: relative;
}

@media (width <= 992px) {
  .form__select-container {
    width: 100%;
  }
}

.form__select-container:after {
  content: url("select-arrow.4f07d9ba.svg");
  position: absolute;
  top: 10px;
  right: 10px;
}

.form__button-container {
  max-width: 340px;
  margin-top: 14px;
}

@media (width <= 992px) {
  .form__button-container {
    max-width: 251px;
  }

  .form__button-container .button-outlined {
    letter-spacing: 1px;
    height: 39px;
    padding-left: 11px;
    font-size: 10px;
  }
}

.dropdown {
  position: relative;
}

@media (width <= 992px) {
  .dropdown {
    margin-bottom: 30px;
  }
}

.dropdown__button {
  letter-spacing: 1.6px;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  text-align: left;
  background: none;
  border: 1px solid #fff;
  align-items: center;
  width: 100%;
  height: 36px;
  padding-left: 20px;
  font-size: 16px;
  display: flex;
  position: relative;
}

@media (width <= 992px) {
  .dropdown__button {
    height: 48px;
  }
}

.dropdown__button_active {
  background: #162a26;
}

.dropdown__button_active:after {
  transform: translateY(-50%)rotate(-180deg);
}

.dropdown__list {
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  letter-spacing: 1.6px;
  color: #fff;
  text-transform: uppercase;
  background: #162a26;
  border: 1px solid #fff;
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
  list-style-type: none;
  transition: all .2s;
  position: absolute;
  top: 36px;
  left: 0;
  overflow: hidden;
}

@media (width <= 992px) {
  .dropdown__list {
    top: 48px;
  }
}

.dropdown__list_visible {
  opacity: 1;
  visibility: visible;
}

.dropdown__list-item {
  cursor: pointer;
  color: #fff;
  margin: 0;
  padding: 15px;
  font-size: 15px;
  line-height: 18px;
  transition: all .2s;
}

.dropdown__list-item_active, .dropdown__list-item:hover {
  color: var(--gold);
}

.dropdown__input_hidden {
  display: none;
}

.form-success-message {
  text-align: left;
  letter-spacing: 1.6px;
  color: #fff;
  padding-top: 60px;
  font: 16px / 24px Brandon Grotesque;
  display: none;
}

iframe {
  width: 100%;
  height: 1325px;
  margin-bottom: -5px;
}

